<template>
  <div class="movie">
    <header>
      <div class="fl">{{cities.city}}</div>
      <van-search
          v-model="value"
          placeholder="请输入搜索影片的标题"
          show-action
          left-icon=" "
          @focus="search()"
        >
        <div slot="action">
          <van-icon name="search" size="20" style="margin-top:6px"/>
        </div>
      </van-search>
    </header>
    <div class="moviebox" ref="moviebox">
      <!-- 方式一 -->
      <!-- <van-tabs v-model="activeName" line-height="2px" title-active-color="#EE0A24" :border="false">
        <van-tab title="热映" name="movielist" to="movielist">
          <div>
            <router-view></router-view>
          </div> 
        </van-tab>
        <van-tab title="影院" name="cinema" to="cinema">
          <div>
             <router-view></router-view>
          </div>
        </van-tab>
        <van-tab title="即将上映" name="coming" to="coming">
          <div>
             <router-view></router-view>
          </div>
        </van-tab>
      </van-tabs> -->
      
      <!-- 方式二 -->
      <van-tabs v-model="activeName" line-height="2px" title-active-color="#EE0A24"
      >
        <van-tab v-for="(item,index) in movieArr" :key="index" 
        :title="item.title" :name="item.name" :to="item.name">
          <div>
            <keep-alive>
              <router-view></router-view>
            </keep-alive>
          </div>
        </van-tab>
      </van-tabs>

    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import { createNamespacedHelpers } from "vuex"
// import { METHODS } from 'http';
const { mapState } = createNamespacedHelpers("movieModule")

export default {
    name: "movie",

    data() {
      return {
        value: '',
        activeName:''
      };
    },

    computed: {
      ...mapState(['cities','movieArr'])
    },

    created() {

      this.activeName = this.$route.name; //纠正头部导航栏激活状态

      let self = this;
      
      //获取用户位置-城市定位 （腾讯API）
      $.ajax({
        type: 'GET',
        url: 'https://apis.map.qq.com/ws/location/v1/ip',
        data:{
          key:'7RHBZ-XCUWR-A63W3-WSKOU-TWDC5-5BBLP',
          output: 'jsonp'
        },
        dataType: 'jsonp',
        success: function (data) {
          // console.log('citydata=>',data);
          self.$store.commit('movieModule/changeLocationCity', data.result.ad_info.city.replace(/市$/, ''))
        }
      })
    },

    methods:{
      // 影片搜索
      search(){
        this.$router.push({name:'searchMovie'});
      }
    }
};
</script>

<style lang="less" scoped>
  .movie{
    /deep/.van-search__content {
      border-radius: 15px;
    }
    header{
      // height: 54px;
      padding-top: 10px;
      margin: 0 auto;
      >div{
        color: #333;
        font-size: 15px;
        padding:0 10px;
        line-height: 32px;
      }
    }
    .moviebox{
      background-color: #f5f5f5;
      position: fixed;
      top:54px;
      bottom: 50px;
      left: 0;
      right: 0;
      // 拉满屏幕
      // ？？？为什么animated会影响这里的样式
      .van-tabs{
        position: static;
      }

      /deep/.van-tab{
        font-size: 16px;
      }
    }
  }
</style>